body {
    background-image: url('https://t3.ftcdn.net/jpg/01/06/80/08/360_F_106800853_kBZQ9XKTrCcEpzFnXChnEymJWnRUjFoK.jpg');
    background-repeat: repeat;
    background-position: right 30px bottom 20px;

}

.js-container img {
    width: 80%;
    display: flex;

    margin-left: auto;
    margin-right: auto;
    align-items: center;

    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

h2 {
    display: flex;
    justify-content: center;
}

.beer_img {
    width: 10%;
}

.beer_link {
    display: flex;
    justify-content: end;
    text-decoration: none;
}